body {
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: lightgray;
}

h2 {
  font-size: 2rem;
  margin-bottom: 30px;
}

.otp-container {
  background-color: #fff;
  padding: 60px 90px;
  border-radius: 10px;
  box-shadow: 0 0 90px rgba(0, 100, 200, 0.5);
  text-align: center;
}

.otp-input {
  width: 40px;
  margin: 5px;
  padding: 5px;
  text-align: center;
  border-radius: 5px;
  background-color: #e3e3e3;
  font-size: 20px;
  text-align: center;
}

.btn {
  padding: 10px 20px;
  border-radius: 20px;
  border: none;
  background-color: #007bff;
  color: #fff;
  font-size: 1rem;
  cursor: pointer;
  margin-top: 15px;
}

.btn:hover {
  background-color: #0056b3;
  scale: 1.1;
}